import {app, nativeImage} from 'electron';
import serve from 'electron-serve';
import { createWindow } from './helpers';

const isProd = process.env.NODE_ENV === 'production';

if (isProd) {
  serve({ directory: 'app' });
} else {
  app.setPath('userData', `${app.getPath('userData')} (development)`);
}

(async () => {
  await app.whenReady();

  const mainWindow = createWindow('main', {
      height: 42,
      width: 180,
      useContentSize: true,
      frame: false,   //
      // resizable: false, //
      transparent: true,  //
      alwaysOnTop: true,  //
      icon: nativeImage.createFromPath('resources/icon.ico'), // "string" || nativeImage.createFromPath('src/image/icons/256x256.ico') path  NativeImage 
      webPreferences: { // 
          devTools: true, //
          nodeIntegration: true, // node node
          webviewTag: true, // <webview>  frame  web 
          webSecurity: false, // 
          contextIsolation: false,
          nodeIntegrationInSubFrames: true // (iframe)(child window)Node.js
      }
  });

  if (isProd) {
    await mainWindow.loadURL('app://./index.html');
  } else {
    const port = process.argv[2];
    await mainWindow.loadURL(`http://localhost:${port}/`);
    // mainWindow.webContents.openDevTools();
  }
})();

app.on('window-all-closed', () => {
  app.quit();
});
